.heading-h2 {
  font-size: clamp(2.6rem, 5vw, 4rem);
}

.p-line {
  font-size: clamp(1.8rem, 1vw, 2rem);
}

.featured__post {
  position: relative;
  font-size: 1.6rem;
  margin-bottom: 14rem;
  transform: translateY(8rem);
  transition: all 1s ease;
}
.featured__post img {
  border-radius: 1rem;
}
.featured__post .text-content {
  position: absolute;
  background: #fff;
  left: 0;
  right: 0;
  bottom: -10rem;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  padding: 2rem;
  padding-top: 1rem;
  text-align: center;
  border-radius: 1rem;
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.1);
}
.featured__post .link {
  text-transform: capitalize;
  color: #ed4c5c;
  display: block;
  font-size: 1.6rem;
}